.doctors {
  background-color: var(--gray-color);

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-top: 70px;
    padding-bottom: 70px;
  }

  &__title {
    margin: 0;
    margin-bottom: 70px;
    text-align: center;
    font-family: Kudryashev Headline;
    font-size: 80px;
    font-weight: 500;
    text-transform: uppercase;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    width: 100%;
    gap: 32px;
  }

  &__item {
    grid-column: 4 span;
    width: 100%;
  }

  &__card {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 47px;
    padding-bottom: 47px;
    min-width: 240px;
    border-radius: 20px;
    border: 1px solid var(--shamp-color);
    // border-image: linear-gradient(244deg, #C78853 33.49%, #D5BDA1 49.48%, #D9D9D9 53.82%, #EFE5D6 56.56%, #D2B589 77.34%);
    // border-image-slice: 1;
  }

  &__img {
    margin-bottom: 16px;
    border-radius: 20px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__name {
    margin: 0;
    margin-bottom: 24px;
    color: var(--, #000);
    text-align: center;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 28px;
    text-transform: uppercase;
  }

  &__descr {
    margin-bottom: 24px;
    text-align: center;
    font-size: 22px;
    font-weight: 400;
    line-height: 40px;
    text-transform: uppercase;
  }

  &__btn {
    padding-top: 16px;
    padding-bottom: 16px;
    padding-left: 26px;
    padding-right: 26px;
  }
}
