.about {
  padding-top: 80px;
  padding-bottom: 80px;
  background-color: var(--gray-color);

  &__title {
    margin: 0;
    margin-bottom: 44px;
    color: var(--dark-color);
    text-align: center;
    font-family: Kudryashev Headline;
    font-size: 80px;
    font-weight: 400;
    line-height: 77px;
    text-transform: uppercase;
  }

  &__content {

    &-top {
      display: flex;
      justify-content: space-between;
      padding-top: 21px;
      padding-bottom: 60px;
    }

    &-bottom {
      display: flex;
      justify-content: space-between;
      padding-top: 60px;
    }

    &-right {
      position: relative;
    }
  }

  &__img {

    &--big {
      justify-self: flex-end;
    }

    &--small {
      position: absolute;
      left: -132px;
      top: 190px;
    }
  }

  &__text {
    margin: 0;
    align-self: center;
    width: 36%;
    font-size: 16px;
    font-weight: 300;
    color: var(--dark-color);
  }

  &__list {
    display: flex;
    padding-top: 50px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    min-width: 166px;

    &:not(:last-child) {
      margin-right: 34px;
    }
  }


  &__amount {
    margin-bottom: 15px;
    padding-left: 10px;
    padding-right: 10px;
    width: 100%;
    text-align: center;
    font-family: Kudryashev Headline;
    font-size: 64px;
    font-weight: 400;
    color: var(--gold-color);
    border-bottom: 1px solid var(--gold-color);
  }

  &__desct {
    text-align: center;
    font-size: 24px;
    font-weight: 300;
    max-width: 166px;
  }

}
